import {ChessPiece} from "./chessPiece";

export class Pawn extends ChessPiece {
    iconWhite = 'A';
    iconBlack = 'a';
    direction = [0, 1];
    initialLine = 1;

    positionsUnderAttack(board) {
        let result;
        if (this.direction[0]) {
            result = [[this.posX + this.direction[0], this.posY - 1], [this.posX + this.direction[0], this.posY + 1]];
        } else {
            result = [[this.posX - 1, this.posY + this.direction[1]], [this.posX + 1, this.posY + this.direction[1]]];
        }
        result = result.filter(pos => {
            return pos[0] >= 0 && pos[0] < board.scaleX && pos[1] >= 0 && pos[1] < board.scaleY
        });
        return result;
    }

    availablePositions(board) {
        let attack = this.positionsUnderAttack(board);

        attack = attack.filter(pos => {
            const piece = board.getPiece(pos[0], pos[1]);
            return piece && piece.player !== this.player;
        });

        let forward = [];
        const pieceForward = board.getPiece(this.posX + this.direction[0], this.posY + this.direction[1]);
        if (!pieceForward) {
            forward.push([this.posX + this.direction[0], this.posY + this.direction[1]]);

            if ((this.direction[0] && this.initialLine === this.posX) || (this.direction[1] && this.initialLine === this.posY)) {
                const farPiece = board.getPiece(this.posX + this.direction[0], this.posY + this.direction[1]);
                if (!farPiece) {
                    forward.push([this.posX + this.direction[0] * 2, this.posY + this.direction[1] * 2]);
                }
            }
        }

        return [...attack, ...forward];
    }
}