export class Board {
    scaleX;
    scaleY;
    chessPieces;
    currentPlayer = 'white';

    fieldIsUnderAttack(x, y, player) {
        return !!this.chessPieces.find(piece => {
            if (piece.player === player) {
                return false;
            }
            const positions = piece.positionsUnderAttack(this);
            return positions.find(pos => pos[0] === x && pos[1] === y);
        })
    }

    getPiece(x, y) {
        return this.chessPieces.find(piece => {
            return piece.posX === x && piece.posY === y
        });
    }
}