<template>
    <div class="container">
        <div class="board">
            <div v-bind:key="row" v-for="row in renderBoard" class="board-row">
                <div v-bind:key="cell" v-for="cell in row"
                     class="board-cell" :class="cell.cellStyleClass">
                    <div class="chess-piece"
                         @click="selectPiece(cell)"
                         :style="{'font-size': cellSize, 'height': cellSize, 'width': cellSize}"
                    >{{ cell.icon }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {initGame} from "./gameModes/default";

    export default {
        name: "Chess",
        methods: {
            selectPiece(cell) {
                const piece = this.game.board.getPiece(cell.posX, cell.posY);
                if (piece) {
                    this.availablePositions = piece.availablePositions(this.game.board);
                    // this.availablePositions = piece.positionsUnderAttack(this.game.board);
                }
                this.selectedPiece = piece;
            }
        },
        beforeCreate() {
        },
        data() {
            return {
                game: initGame(),
                selectedPiece: null,
                availablePositions: []
            }
        },
        computed: {
            renderBoard() {
                const board = [];
                for (let i = this.game.board.scaleY - 1; i >= 0; i--) {
                    const row = [];
                    for (let j = 0; j < this.game.board.scaleX; j++) {
                        const cell = {};
                        const piece = this.game.board.chessPieces.find(p => {
                            return p.posX === j && p.posY === i;
                        });
                        if (piece) {
                            cell.icon = piece.iconWhite;
                            cell.posX = piece.posX;
                            cell.posY = piece.posY;
                            if (piece.player === 'black') {
                                cell.icon = piece.iconBlack;
                            }
                        }
                        if (this.availablePositions.find(ap => ap[0] === j && ap[1] === i)) {
                            cell.cellStyleClass = 'available-pos';
                        }
                        row.push(cell);
                    }
                    board.push(row)
                }
                return board;
            },
            cellSize() {
                const boardSize = Math.max(3, this.game.board.scaleY, this.game.board.scaleX);
                if (window.innerHeight > window.innerWidth) {
                    return Math.floor(100 / boardSize * 0.9) + 'vw';
                } else {
                    return Math.floor(100 / boardSize * 0.9) + 'vh';
                }
            }
        }
    }

</script>

<style scoped>
    .board-row {
        display: flex;
        width: auto;
        clear: both;
    }

    .board-cell {
        float: left;
        width: 100%;
        height: 100%;
    }

    .board .board-row:nth-child(even) .board-cell:nth-child(odd).available-pos,
    .board .board-row:nth-child(odd) .board-cell:nth-child(even).available-pos {
        background: #dba752;
        box-shadow: inset 0 0 0 5px #c28f4e;
    }
    .board .board-row:nth-child(odd) .board-cell:nth-child(odd).available-pos,
    .board .board-row:nth-child(even) .board-cell:nth-child(even).available-pos {
        background: #ffdd9a;
        box-shadow: inset 0 0 0 5px #d6aa6c;
    }

    .board .board-row:nth-child(even) .board-cell:nth-child(odd),
    .board .board-row:nth-child(odd) .board-cell:nth-child(even) {
        background: #d18b47;
    }
    .board .board-row:nth-child(odd) .board-cell:nth-child(odd),
    .board .board-row:nth-child(even) .board-cell:nth-child(even) {
        background: #ffce9e;
    }

    .board {
        display: flex;
        flex-direction: column;
    }

    .chess-piece {
        font-family: stchess, serif;
    }

    .container {
        width: 100%;
        display: flex;
        justify-content: center;
        height: 100vh;
        align-items: center;
    }

</style>